
import { defineComponent, reactive, toRefs } from 'vue'
import { useRouter } from 'vue-router'

interface tsData {
  showLeftImg: boolean
}

export default defineComponent({
  components: {},
  setup () {
    const router = useRouter()
    const data: tsData = reactive({
      showLeftImg: false
    })
    const refData = toRefs(data)

    const changeLeftStatus = () => {
      data.showLeftImg = false
    }
    const changeRightStatus = () => {
      data.showLeftImg = true
    }
    const goPath = (path:string):void =>{
      router.push(path)
    }
    return {
      ...refData,
      changeRightStatus,
      changeLeftStatus,
      goPath
    }
  }
})
