<template>
  <div class="brand-intro" :class="{active: showLeftImg}" >
    <div class="pic-wrap left" @mouseenter.stop="changeLeftStatus">
      <div class="brand-code" @click.stop="goPath('/culture')">
        <p class="txt">品牌文化</p>
        <p class="txt-en">Brand Culture</p>
      </div>
    </div>
    <div class="pic-wrap right" @mouseenter.stop="changeRightStatus">
      <div class="brand-code" @click.stop="goPath('/brand')">
        <p class="txt">品牌框架</p>
        <p class="txt-en">Brand Framework</p>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive, toRefs } from 'vue'
import { useRouter } from 'vue-router'

interface tsData {
  showLeftImg: boolean
}

export default defineComponent({
  components: {},
  setup () {
    const router = useRouter()
    const data: tsData = reactive({
      showLeftImg: false
    })
    const refData = toRefs(data)

    const changeLeftStatus = () => {
      data.showLeftImg = false
    }
    const changeRightStatus = () => {
      data.showLeftImg = true
    }
    const goPath = (path:string):void =>{
      router.push(path)
    }
    return {
      ...refData,
      changeRightStatus,
      changeLeftStatus,
      goPath
    }
  }
})
</script>

