<template>
  <div class="brand-intro-mobile" :class="{active: showLeftImg}">
    <div class="pic-wrap left" @touchstart.stop="changeStart" @touchmove.stop="changeUpMove">
      <div class="brand-code" @click.stop="goPath('/culture')">
        <p class="txt">品牌文化</p>
        <p class="txt-en">Brand Culture</p>
      </div>
    </div>
    <div class="pic-wrap right" @touchstart.stop="changeStart" @touchmove.stop="changeDownMove">
      <div class="brand-code" @click.stop="goPath('/brand')">
        <p class="txt">品牌框架</p>
        <p class="txt-en">Brand Framework</p>
      </div>
    </div>
  </div>
</template>

<script lang='ts'>

import { defineComponent, reactive, toRefs } from 'vue'
import { useRouter } from 'vue-router'
interface tsData {
  showLeftImg: boolean
}
export default defineComponent({
  components: {},
  setup () {
    const router = useRouter()
    let startY: number = 0
    const data: tsData = reactive({
      showLeftImg: false
    })
    const refData = toRefs(data)
    const changeStart: (e: any) => void = (e: any): void => {
      startY = e.changedTouches[0].clientY
    }
    const changeUpMove: (e: any) => void = (e: any): void => {
      const endY = e.changedTouches[0].clientY
      if (startY - endY < 0) {
        // 往上滑
        data.showLeftImg = false
      }
    }
    const changeDownMove: (e: any) => void = (e: any): void => {
      const endY = e.changedTouches[0].clientY
      if (startY - endY > 0) {
        // 往下滑
        data.showLeftImg = true
      }
    }
    const goPath = (path:string):void =>{
      router.push(path)
    }
    return {
      ...refData,
      changeUpMove,
      changeStart,
      changeDownMove,
      goPath
    }
  }
})
</script>

